/* LLM-generated CSS*/
.box {
    --dot-color: var(--COLOR_MEDIUM);
    --dot-size: 0.05dvh;
    --dot-spacing: 0.3dvh;

    background-color: var(--COLOR_LIGHT);
    background-image: radial-gradient(
        circle,
        var(--dot-color) var(--dot-size), /* Dot color up to dot size */
        transparent var(--dot-size) /* Transparent beyond the dot size */
    );
    background-size: var(--dot-spacing) var(--dot-spacing); /* Size of one pattern tile */
    border: 0.1dvh solid black;
}

.binary-box {
    --dot-color: var(--COLOR_MEDIUM);
    --dot-size: 0.05dvh;
    --dot-spacing: 0.3dvh;
    
    background-color: var(--COLOR_LIGHT);
    background-image: radial-gradient(
        circle,
        var(--dot-color) var(--dot-size), /* Dot color up to dot size */
        transparent var(--dot-size) /* Transparent beyond the dot size */
    );
    background-size: var(--dot-spacing) var(--dot-spacing); /* Size of one pattern tile */
    border: 0.1dvh solid black;
}