:root {
  --COLOR_VERYLIGHT: #bdfad4;
  --COLOR_LIGHT: #72db9b;
  --COLOR_MEDIUM: #66aec0;
  --COLOR_MEDIUMDARK: rgb(0, 76, 138);
  --COLOR_DARK: #003b6b;
  --COLOR_HIGHLIGHTED: #fff345;
  --FONT: "Lexend Deca", sans-serif;
}

.App {
  text-align: center;
}

.App-header {
  min-height: 100dvh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  font-size: 3.5svh;
  color: white;
}

.Task {
  border: 0.2dvh solid black;
  width: 90%;
  height: 77dvh;
  border-radius: 1.5svh;
  box-shadow: 0svh 0svh 0.6svh rgba(0, 0, 0, 0.5);
  background-color: var(--COLOR_MEDIUM);
  position: relative;
  box-sizing: border-box;
}

a:link {
  color: var(--COLOR_LIGHT);
  background-color: transparent;
  
}

a:visited {
  color: var(--COLOR_LIGHT);
  background-color: transparent;
}

